@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* events map */
$events-map-space: 30px;
$events-map-space--mob-lg: 20px;
$events-map-side-width: 290px;

.events-map {
	background-color: white;
	padding: $events-map-space;
	position: relative;
	@include clear;

	@include media('<mob-lg') {
		padding: $events-map-space--mob-lg; } }

.events-map__main {
	//float: left
	//width: 100%

	@include media('<desk-lg') {
		//float: none

		&:last-child {
			.events-map__content {
				margin-bottom: 0; } } } }

.events-map__content {
	//margin-right: calc(#{$events-map-side-width} + #{$events-map-space*2})
	@include push-auto;
	width: 735px;
	max-width: 100%;

	@include media('<desk-lg') {
		margin-bottom: $events-map-space; } }

.events-map__side {

	@include media('<desk-lg') {
		float: none;
		@include push-auto; } }

.events-map__banner {
	margin-bottom: $bottom-space;
	max-width: 420px;
	display: none;

	&:last-child {
		margin-bottom: 0; }

	a {
		display: block; }

	img {
		width: auto; }

	table {
		margin-bottom: 0;

		td, th {
			padding: 0; } }

	& > table {
		& > tbody {
			& > tr {
				& > td {
					padding: 5px 15px; } } } } }

#phenomena_map .info_block {
	//right: -10px !important
	font-size: 1.4rem!important;

	@include media('>=960px') {
		right: -300px !important;

		&.info_closed {
			right: -23px !important; } }

	@include media('<960px') {
		right: 0 !important;

		// делаем инверсию. при загрузке закрыто. на клик открываем
		width: 23px!important;
		overflow: hidden !important;
		max-height: 90% !important;

		.info_activity {
			background: url(http://101.belta.by/desimages/menu_icon_open.png) no-repeat !important;
			left: auto !important;
			right: 5px !important;
			width: 9px !important;
			height: 17px !important;
			-webkit-transform: rotate(0.5turn);
			-ms-transform: rotate(0.5turn);
			transform: rotate(0.5turn); }

		.obl_gr {
			padding-left: 50px;

			&:hover .phen_item, &:hover .phen_item2 {
				display: none !important; } }

		&.info_closed {
			width: 300px !important;
			overflow: visible !important;
			max-height: none !important;

			.info_activity {
				background: url(http://101.belta.by/desimages/close.png) no-repeat !important;
				right: 10px !important;
				width: 24px !important;
				height: 24px !important; }

			.obl_gr {
				padding-left: 0 !important;

				&:hover .phen_item, &:hover .phen_item2 {
					display: block !important; } } } } }

