/* phenomena map restyling */
.phenomena-map {
	&#phenomena_map .svg_map {
		max-width: 900px;
		background: white !important;
		@include push-auto; }

	&#phenomena_map .menu_pozar_b div,
	&#phenomena_map .menu_pozar_sel div,
	&#phenomena_map .menu_main_sel,
	&#phenomena_map .obl_title,
	&#phenomena_map .phen_img {

		font-size: 1.4rem; }

	&#phenomena_map .phen_item,
	&#phenomena_map .copyright,
	&#phenomena_map .copyright_podtopl,
	&#phenomena_map .dev_info {
		font-size: 1.2rem; }

	&#phenomena_map .more,
	&#phenomena_map .more2 {
		//font-size: 1.1rem
		font-size: 14px; }

	&#phenomena_map .svg_legenda {
		top: 30px;
		position: relative;

		@include media('<mob-lg') {
			width: 100%; } }

	&#phenomena_map .svg_legenda_text {
		font-size: 2rem;

		@include media('<mob-lg') {
			font-size: 2.3rem; } }



	.phen_info {
		font-size: 14px; }

	.phen_item,
	.copyright,
	.copyright_podtopl,
	.dev_info {
		font-size: 14px!important; }

	.phen_info_hover {
		font-size: 14px!important; }

	.phen_info_hover a {
		font-size: 14px !important; }

	.rep_link_s {
		font-size: 14px !important; }

	.phenomenon_info_inner {
		left: 50% !important;
		top: 0 !important;
		transform: translateX(-50%); } }

.weather {
	background: #fff;
	margin: 0 auto;
	padding: 1rem;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box; }

/* phenomena map restyling end */
